import {
  AfterViewInit,
  Component,
  DestroyRef,
  ElementRef,
  HostListener,
  OnDestroy,
  OnInit,
  Renderer2,
  computed,
  effect,
  inject,
  input,
  signal,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { Subject, debounceTime } from 'rxjs';

import { SplitterModule } from 'primeng/splitter';
import { ButtonModule } from 'primeng/button';

import {
  LayoutMode,
  LayoutState,
  PanelConfig,
  PanelState,
} from '../../../models/layout.model';
import { LocalStorageService } from '../../../services/local-storage.service';
import {
  hasProperty,
  isArray,
  isDefined,
  isEmpty,
  isNullOrUndefined,
  isNumber,
  isObject,
  isString,
} from '../../../utils';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';

@Component({
  selector: 'app-adaptive-layout',
  imports: [CommonModule, SplitterModule, ButtonModule],
  templateUrl: './adaptive-layout.component.html',
  styleUrl: './adaptive-layout.component.scss',
})
export class AdaptiveLayoutComponent
  implements OnInit, OnDestroy, AfterViewInit
{
  private readonly localStorage = inject(LocalStorageService);
  private readonly destroyRef = inject(DestroyRef);

  readonly panels = input<PanelConfig[]>([]);
  readonly height = input<string>('100%');
  readonly stateKey = input<string>('splitter-state');
  // Accept numbers (percentage) or pixel strings (e.g. '64px')
  readonly defaultSizes = input<(number | string)[]>([]);
  readonly minSizes = input<(number | string)[]>([]);
  readonly defaultLayout = input<LayoutMode>('horizontal');
  readonly responsive = input<boolean>(true);

  private readonly resizeSubject = new Subject<void>();
  private static readonly DEFAULT_MIN_SIZE = 0;
  private static readonly DEFAULT_MAX_SIZE = 100;
  // Observe container size to normalize when measurable and on resizes
  private resizeObserver: ResizeObserver | null = null;

  readonly panelSizes = input<(number | string)[]>([25, 75]);

  // Add a writable signal for managing current panel sizes
  private readonly currentPanelSizes = signal<number[]>([]); // always stored internally as percentages

  // Regular property for template binding to avoid function calls
  panelSizesForTemplate: number[] = [25, 75]; // normalized percentages passed to splitter

  // Keep original raw inputs for recalculation (to support px on resize)
  private rawPanelSizes: (number | string)[] = [25, 75];
  private rawMinSizes: (number | string)[] = [];

  // Add a getter that ensures we always return a valid array
  getSafePanelSizes(): number[] {
    const sizes = this.panelSizesForTemplate;
    if (!Array.isArray(sizes) || sizes.length === 0) {
      return [25, 75];
    }
    return sizes;
  }

  // Computed signal for template binding
  readonly computedPanelSizes = computed(() => {
    const sizes = this.currentPanelSizes();
    const defaultSizes = this.defaultSizes();
    const inputSizes = this.panelSizes();

    // Use current sizes if available, otherwise fallback to defaults
    if (Array.isArray(sizes) && sizes.length > 0) {
      return sizes;
    } else if (Array.isArray(defaultSizes) && defaultSizes.length > 0) {
      return defaultSizes;
    } else if (Array.isArray(inputSizes) && inputSizes.length > 0) {
      return inputSizes;
    }
    return [25, 75]; // Final fallback
  });

  panelStates: PanelState[] = [];
  stateStorage = 'session';
  currentLayout: LayoutMode = 'horizontal';
  currentHeight = '100%';
  containerClass = '';
  touchStartX = 0;
  private readonly elementRef = inject(ElementRef);
  private readonly renderer = inject(Renderer2);

  constructor() {
    this.setupResizeListener();

    // Initialize current panel sizes from input
    effect(() => {
      const inputSizes = this.panelSizes();
      const defaultSizes = this.defaultSizes();
      if (Array.isArray(inputSizes) && inputSizes.length > 0) {
        this.rawPanelSizes = [...inputSizes];
      } else if (Array.isArray(defaultSizes) && defaultSizes.length > 0) {
        this.rawPanelSizes = [...defaultSizes];
      } else {
        this.rawPanelSizes = [25, 75];
      }
      this.normalizeAndSetPanelSizes();
    });

    effect(() => {
      // Read the current panel sizes signal to track it
      const sizes = this.currentPanelSizes();

      // Skip immediate execution during initialization
      if (isNullOrUndefined(this.elementRef?.nativeElement)) return;

      // Apply hidden panel styles with a small delay to ensure DOM is ready
      setTimeout(() => this.applyHiddenPanelStyles(sizes), 0);
    });
  }

  ngOnInit() {
    this.initializePanelStates();
    this.loadSavedState();
    this.currentHeight = this.height();

    // Ensure panel sizes are properly initialized
    if (
      !Array.isArray(this.panelSizesForTemplate) ||
      this.panelSizesForTemplate.length === 0
    ) {
      this.normalizeAndSetPanelSizes();
    }
  }

  ngOnDestroy() {
    this.resizeSubject.complete();

    // Clean up the ResizeObserver
    if (this.resizeObserver) {
      this.resizeObserver.disconnect();
      this.resizeObserver = null;
    }
  }

  ngAfterViewInit() {
    // Apply styling after view initialization
    this.applyHiddenPanelStyles(this.currentPanelSizes());

    // Normalize once the view is painted to ensure container has dimensions
    requestAnimationFrame(() => this.normalizeAndSetPanelSizes());

    // Observe container for dimension changes and (re)normalize
    const container: HTMLElement =
      this.elementRef.nativeElement.querySelector('.adaptive-layout') ??
      this.elementRef.nativeElement;

    const ResizeObserverCtor = (
      globalThis as unknown as { ResizeObserver?: typeof ResizeObserver }
    ).ResizeObserver;
    if (typeof ResizeObserverCtor === 'function') {
      this.resizeObserver = new ResizeObserverCtor((entries) => {
        for (const entry of entries) {
          if (entry.contentRect.width > 0) {
            this.resizeSubject.next();
          }
        }
      });
      this.resizeObserver.observe(container);
    }
  }

  // Add a getter for the template to use
  getCurrentPanelSizes(): number[] {
    const sizes = this.currentPanelSizes();
    // Ensure we always return a valid array
    return Array.isArray(sizes) && sizes.length > 0 ? sizes : [25, 75];
  }

  // Add defensive getters for template
  getPanels(): PanelConfig[] {
    const panels = this.panels();
    return Array.isArray(panels) ? panels : [];
  }

  getMinSizes(): number[] {
    const minSizesInput = this.minSizes();
    if (Array.isArray(minSizesInput)) {
      this.rawMinSizes = [...minSizesInput];
      return this.normalizeValues(minSizesInput);
    }
    return [];
  }

  /**
   * Apply special styles to panels that should be hidden
   * @param panelSizes The current panel sizes to evaluate
   */
  private applyHiddenPanelStyles(panelSizes: number[]) {
    if (isNullOrUndefined(this.elementRef?.nativeElement)) return;

    if (isEmpty(panelSizes)) return;

    const hasHiddenPanel = panelSizes.includes(0);

    if (hasHiddenPanel) {
      this.renderer.addClass(this.elementRef.nativeElement, 'has-hidden-panel');
    } else {
      this.renderer.removeClass(
        this.elementRef.nativeElement,
        'has-hidden-panel',
      );
    }

    // Apply individual panel classes
    const panelElements =
      this.elementRef.nativeElement.querySelectorAll('.p-splitter-panel');

    if (!isEmpty(panelElements)) {
      for (const [index, size] of panelSizes.entries()) {
        if (index < panelElements.length) {
          const panel = panelElements[index];
          if (size === 0) {
            this.renderer.addClass(panel, 'zero-width-panel');
            // Also hide the splitter gutter if this panel is zero-width
            const gutter = this.elementRef.nativeElement.querySelector(
              `.p-splitter-gutter:nth-of-type(${index * 2 + 1})`,
            );
            if (isDefined(gutter)) {
              this.renderer.setStyle(gutter, 'display', 'none');
            }
          } else {
            this.renderer.removeClass(panel, 'zero-width-panel');
            // Make gutter visible again
            const gutter = this.elementRef.nativeElement.querySelector(
              `.p-splitter-gutter:nth-of-type(${index * 2 + 1})`,
            );
            if (isDefined(gutter)) {
              this.renderer.removeStyle(gutter, 'display');
            }
          }
        }
      }
    }
  }

  private setupResizeListener() {
    this.resizeSubject
      .pipe(debounceTime(150), takeUntilDestroyed(this.destroyRef))
      .subscribe({
        next: () => this.normalizeAndSetPanelSizes(),
        error: (error) => console.error('Resize error:', error),
      });
  }

  @HostListener('window:resize')
  onResize() {
    if (this.responsive()) {
      this.resizeSubject.next();
    }
    // Debounced via resizeSubject
  }

  private initializePanelStates() {
    if (!isArray(this.panels())) {
      console.warn('No panels provided for initialization');
      return;
    }

    this.panelStates = this.panels().map((panel, _index) => {
      const defaultSize = 100 / this.panels().length;

      return {
        size: isNumber(defaultSize) ? defaultSize : 0,
        collapsed: false,
        minSize:
          isDefined(panel) && isNumber(panel.minSize)
            ? panel.minSize
            : AdaptiveLayoutComponent.DEFAULT_MIN_SIZE,
        maxSize:
          isDefined(panel) && isNumber(panel.maxSize)
            ? panel.maxSize
            : AdaptiveLayoutComponent.DEFAULT_MAX_SIZE,
        hidden: false,
      };
    });
  }

  private loadSavedState() {
    try {
      const savedState: LayoutState | null =
        this.localStorage.getItem<LayoutState>(this.stateKey());
      if (!savedState) {
        this.currentLayout = this.defaultLayout();
        return;
      }

      const state: LayoutState = savedState;
      if (!this.isValidState(state)) {
        return;
      }

      this.panelStates = state.panelStates;
      this.currentLayout = state.layoutMode;

      // Update panel sizes if they exist in the saved state
      if (Array.isArray(state.panelSizes) && state.panelSizes.length > 0) {
        const validSizes = state.panelSizes.filter(
          (size) => typeof size === 'number' && !Number.isNaN(size),
        );
        if (validSizes.length > 0) {
          this.currentPanelSizes.set([...validSizes]);
          this.panelSizesForTemplate = [...validSizes];
        }
      }
    } catch (e) {
      console.error('Error loading saved state:', e);
    }
  }

  private isValidState(state: unknown): state is LayoutState {
    return (
      isObject(state) &&
      hasProperty(state, 'panelStates') &&
      hasProperty(state, 'panelSizes') &&
      hasProperty(state, 'layoutMode') &&
      Array.isArray(state['panelStates']) &&
      Array.isArray(state['panelSizes']) &&
      isString(state['layoutMode'])
    );
  }

  // Touch support handlers
  private calculatePercentageMoved(diff: number): number {
    return (diff / window.innerWidth) * 100;
  }

  private updatePanelSize(panelIndex: number, percentageMoved: number): void {
    if (!isNumber(panelIndex) || !isNumber(percentageMoved)) {
      console.warn('Invalid parameters for panel size update');
      return;
    }

    const currentPanel = this.panelStates[panelIndex];
    if (!isDefined(currentPanel)) {
      console.warn('Panel not found at index:', panelIndex);
      return;
    }

    const minSize = isNumber(currentPanel.minSize) ? currentPanel.minSize : 10;
    const maxSize = isNumber(currentPanel.maxSize) ? currentPanel.maxSize : 90;
    const currentSizes = this.currentPanelSizes();
    const currentSize = currentSizes[panelIndex];

    if (!isNumber(currentSize)) {
      console.warn('Invalid current panel size');
      return;
    }

    const newSize = Math.max(
      minSize,
      Math.min(maxSize, currentSize + percentageMoved),
    );

    if (newSize !== currentSize) {
      // Update the signal with a new array
      const newSizes = [...currentSizes];
      newSizes[panelIndex] = newSize;

      // Validate the new sizes array
      const validSizes = newSizes.filter(
        (size) => typeof size === 'number' && !Number.isNaN(size),
      );
      if (validSizes.length > 0) {
        this.currentPanelSizes.set(validSizes);
        this.panelSizesForTemplate = validSizes;
      }
    }
  }

  private getPanelIndex(touchX: number): number {
    let cumulativeWidth = 0;
    const sizes = this.currentPanelSizes();
    for (let i = 0; i < sizes.length; i++) {
      cumulativeWidth += (sizes[i] / 100) * window.innerWidth;
      if (touchX < cumulativeWidth) {
        return i;
      }
    }
    return sizes.length - 1; // Default to the last panel if not found
  }

  handleTouchStart(event: TouchEvent) {
    this.touchStartX = event.touches[0].clientX;
  }

  handleTouchMove(event: TouchEvent) {
    const touchX = event.touches[0].clientX;
    const diff = touchX - this.touchStartX;

    if (Math.abs(diff) > 10) {
      event.preventDefault();
      const percentageMoved = this.calculatePercentageMoved(diff);
      const panelIndex = this.getPanelIndex(touchX);
      this.updatePanelSize(panelIndex, percentageMoved);
    }
  }

  handleTouchEnd(_event: TouchEvent) {
    this.touchStartX = 0;
  }

  // ---------------- New normalization logic ----------------

  /** Normalize raw panelSizes (number | 'Npx') to percentages and update signals */
  private normalizeAndSetPanelSizes(): void {
    // Guard: wait until container has a measurable width
    const width = this.getContainerWidth();
    if (width <= 0) {
      // Try again on next frame
      requestAnimationFrame(() => this.normalizeAndSetPanelSizes());
      return;
    }

    const normalized = this.normalizeValues(this.rawPanelSizes);
    // Ensure sum = 100
    const sum = normalized.reduce((a, b) => a + b, 0);
    let final = normalized;
    if (sum !== 100 && sum > 0) {
      final = normalized.map((v) => (v / sum) * 100);
    }
    this.currentPanelSizes.set(final);
    this.panelSizesForTemplate = final;
  }

  /** Normalize an array of (number|string) to percentages; pixel entries converted relative to container width */
  private normalizeValues(values: (number | string)[]): number[] {
    if (!Array.isArray(values) || values.length === 0) return [];
    const cw = this.getContainerWidth();
    return values.map((v) => this.toPercent(v, cw));
  }

  private toPercent(value: number | string, containerWidth: number): number {
    if (typeof value === 'number') {
      return Number.isFinite(value) && value >= 0 ? value : 0;
    }
    const trimmed = value.trim().toLowerCase();
    if (trimmed.endsWith('px')) {
      const px = Number.parseFloat(trimmed.slice(0, -2));
      if (!Number.isFinite(px) || px < 0) return 0;
      return containerWidth > 0 ? (px / containerWidth) * 100 : 0;
    }
    // numeric string
    const num = Number.parseFloat(trimmed);
    return Number.isFinite(num) && num >= 0 ? num : 0;
  }

  private getContainerWidth(): number {
    const el: HTMLElement | null =
      this.elementRef?.nativeElement?.querySelector('.adaptive-layout') ??
      this.elementRef?.nativeElement;
    const width = el?.clientWidth ?? 0;
    return Number.isFinite(width) && width > 0 ? width : 0;
  }
}
